/* 6. Write CSS rules that cause the header and .collage div to be centered on the page */

/* 7. Write CSS rules that ensure that no more than 4 photos appear on a row */

body{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    
}

.collage {
    width: 70vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    border: 1px solid black;
}

img {
    margin: 2px;
    width: 15vw;
    min-width: 150px;
}




